import React, {Component} from "react";
import PropTypes from "prop-types";

import MulticartDocumentLink from "../../../documents/components/MulticartDocumentLink";
import {Expander, Section, Toggle} from "../../../core/components/ui/Expander";

class OraDocumentsSectionCategory extends Component {
    static propTypes = {
        header: PropTypes.string,
        documents: PropTypes.arrayOf(PropTypes.shape({
            documentCode: PropTypes.string,
            documentDescription: PropTypes.string,
            bundleNo: PropTypes.number
        }))
    };

    render() {
        const toggleHeader = (
            <h2 className="h4 opl-section__heading">{this.props.header}</h2>
        );

        const sectionHeader = (
            <div className="l-row">
                <Toggle variant="arrow" header={toggleHeader}
                        className="l-col l-col-12 "
                        triggerClassName="opl-document__expander__trigger">
                    <span className="opl-section__text-show">rozwiń sekcję Inne</span>
                    <span className="opl-section__text-hide">zwiń sekcję Inne</span>
                </Toggle>
            </div>
        );

        return (
            <Expander
                variant="section"
                sectionClassName="opl-checkout-section"
                summaryClassName="opl-section__border-bottom"
                toggleClassName="h4 opl-checkout-section__heading"
            >
                <Section header={sectionHeader}>
                    <div className="u-padding-left-l-xl u-small-no-padding-l u-padding-m">
                        <ul>
                            {
                                (this.props.documents) &&
                                <div>
                                    {
                                        this.props.documents.map(document => {
                                            const key = document.documentCode + (document.bundleNo ? "_" + document.bundleNo : '');
                                            return <MulticartDocumentLink key={key} {...document} />
                                        })
                                    }
                                </div>
                            }
                        </ul>
                    </div>
                </Section>
            </Expander>
        )
    }
}

export default OraDocumentsSectionCategory;